import React from 'react';
import './App.css';
import { ReactComponent as ChromiaLogo } from './assets/logo.svg';
// @ts-ignore
import detectProvider from '@metamask/detect-provider';
import Web3 from 'web3';

import {
  Box,
  Button,
  CircularProgress,
  Grid,
  TextField,
  Typography,
  Paper, List, ListItem, Container, Link, makeStyles,
} from '@material-ui/core';
import {ERC20_ABI, TWN_ABI} from "./abi";

interface Operational {
  web3: Web3;
  chrTokenContract: any;
  twnContract: any;
}

interface Balances {
  chrBalanceRaw: string;
  chrBalance: number;
  depositedCHRBalance: number;
  until: number;
  haveAllowance: boolean;
  totalStaked: number;
  canWithdraw: boolean;
}

const useStyles = makeStyles({
  '@global': {
    body: {
      fontFamily: '"NB-International", "Roboto", "Helvetica", sans-serif',
      color: '#fff8f8'
    },
  },
})

const useMainStyles = makeStyles({
  main: {
    fontSize: 18,
  },
})

const TWN_CONTRACT_ADDRESS = '0xC7b0F970c1EFBB181194Fc15ccD5C4a2c2Ab863B';

const StakeMain: React.FunctionComponent = () => {
  const classes = useMainStyles()
  const [provider, setProvider] = React.useState(null);
  const [haveAccounts, setHaveAccounts] = React.useState(false);
  const [selectedAddress, setSelectedAddress] = React.useState('');
  const [operational, setOperational] = React.useState<Operational | null>(null);
  const [balances, setBalances] = React.useState<Balances | null>(null);
  const [loading, setLoading] = React.useState(false);
  const [errorMsg, setErrorMsg] = React.useState('');
  const [sumToStake, setSumToStake] = React.useState(1000);
  const [forceHaveAllowance, setForceHaveAllowance] = React.useState(false);
  const depositsClosed = false;

  const withdrawPossible = balances &&
      (balances.depositedCHRBalance > 0) &&
      (balances.until > 0) &&
      (balances.until <= Date.now());

  async function loadBaseData () {
    const _provider = await detectProvider();
    if (!_provider) return;
    _provider.enable();
    setProvider(_provider);
    setHaveAccounts((await _provider.request({ method: 'eth_accounts' })).length > 0);
    const _selectedAddress = _provider.selectedAddress;
    _provider.on('accountsChanged', () => {
      window.location.reload();
    });
    setSelectedAddress(_selectedAddress);
    const web3 = new Web3(_provider);
    setOperational({
      web3,
      chrTokenContract: new web3.eth.Contract(ERC20_ABI, '0x8a2279d4a90b6fe1c4b30fa660cc9f926797baa2'),
      twnContract: new web3.eth.Contract(TWN_ABI, TWN_CONTRACT_ADDRESS)
    });
  }

  async function loadDetailData () {
    if (!operational) return;
    const res1 = await operational.twnContract.methods.getStakeState(selectedAddress).call();
    const res2 = await operational.chrTokenContract.methods.balanceOf(selectedAddress).call();
    const res3 = await operational.twnContract.methods.estimateAccumulated(selectedAddress).call();
    const haveAllowance = parseFloat(await operational.chrTokenContract.methods.allowance(
            selectedAddress, TWN_CONTRACT_ADDRESS
        ).call()) > 0;
    const totalStaked = parseFloat(await operational.chrTokenContract.methods.balanceOf(TWN_CONTRACT_ADDRESS).call())/1e6;
    const depositedCHRBalance = parseFloat(res1[0]) / 1e6;
    const until = parseFloat(res1[2]) * 1000;
    setBalances({
      canWithdraw: (depositedCHRBalance > 0 && until > 0
          && until < Date.now()),
      chrBalanceRaw: res2,
      chrBalance: parseFloat(res2) / 1e6,
      depositedCHRBalance,
      until,
      haveAllowance: forceHaveAllowance || haveAllowance,
      totalStaked
    });
  }


  React.useEffect( () => { loadBaseData() }, []);
  React.useEffect( () => { loadDetailData() }, [operational, loading]);

  const approve = async () => {
    if (!operational) return;
    setLoading(true); setErrorMsg('');
    try {
      await operational.chrTokenContract.methods.approve(
          TWN_CONTRACT_ADDRESS,
          operational.web3.utils.toWei("1", "finney") // that's 1 billion CHR :)
      ).send({from: selectedAddress});
      setForceHaveAllowance(true);
      await loadDetailData();
    } catch (e) {
      setErrorMsg('Got error:' + e.message);
    }
    setLoading(false);
  };

  const stake = async () => {
    if (!operational || !balances) return;
    if (sumToStake < Math.max(1000, balances.depositedCHRBalance)) {
      setErrorMsg(`Staked amount should be at least ${Math.max(1000, balances.depositedCHRBalance)}`);
      return;
    }

    setLoading(true); setErrorMsg('');
    try {
      await operational.twnContract.methods.stake(
          Math.round(sumToStake * 1e6), 86400*14
      ).send({from: selectedAddress});
      await loadDetailData();
    } catch (e) {
      setErrorMsg('Got error: ' + e.message);
    }
    setLoading(false);
  };
/*
  const unstake = async () => {
    if (!operational || !balances) return;
    setLoading(true); setErrorMsg('');
    try {
      await operational.rewardContract.methods.withdraw(selectedAddress)
          .send({from: selectedAddress});
      await loadDetailData();
    } catch (e) {
      setErrorMsg('Got error: ' + e.message);
    }
    setLoading(false);
  };*/

  const mainContent = (balances: Balances) => {
    return (<>
      <Grid item container className={classes.main}>
        <List disablePadding>
          <ListItem disableGutters dense>Total staked by all users: {balances.totalStaked.toFixed(2)} CHR
          </ListItem>
          <ListItem disableGutters dense>Address: {selectedAddress}</ListItem>
          <ListItem disableGutters dense>CHR balance: {balances.chrBalance.toFixed(2)} CHR</ListItem>
          <ListItem disableGutters dense>Your current stake: {balances.depositedCHRBalance.toFixed(2)} CHR</ListItem>
          <ListItem disableGutters dense>Withdraw requested: {
            balances.until > 0 ?
                "Yes" : "No"
          }</ListItem>
          {balances.until > 0 && !withdrawPossible &&
          <ListItem disableGutters dense>Frozen until: {new Date(balances.until).toLocaleDateString(window.navigator.language)}
          </ListItem>
          }
        </List>
      </Grid>
      {
        depositsClosed && <Typography variant={"h4"}>
          Deposits are closed
        </Typography>
      }
      {
        !depositsClosed && !balances.haveAllowance && (balances.chrBalance > 0) &&
        <Box width={200} mt={2}>
          <Button color="secondary" variant="contained" onClick={() => approve()} disabled={loading} fullWidth>
            Approve
            {loading && <CircularProgress />}
          </Button>
        </Box>
      }
      {
        !depositsClosed &&(balances.haveAllowance) && (balances.chrBalance > 0) &&
        <Box mt={2}>
          <Grid item container className={classes.main}>
            <TextField
              variant="outlined"
              label="Amount to stake"
              type="number"
              value={sumToStake}
              inputProps={{min: 1000}}
              onChange={(e) => setSumToStake(parseFloat(e.target.value))}
            />
            <Box width={200} ml={4}>
              <Button variant="contained" color="secondary" onClick={() => stake()}
                      disabled={loading} fullWidth>
                Stake
                {loading && <CircularProgress />}
              </Button>
            </Box>
          </Grid>
        </Box>
      }
      {/*
        balances.canWithdraw &&
        <Grid item container>
          <Button variant="contained" color="primary" onClick={() => unstake()} disabled={loading}>
            Withdraw
            {loading && <CircularProgress />}
          </Button>
        </Grid>
      */}
      </>);
  };

  return (
      <Grid container spacing={2} direction="column">
        <Grid item>
          <ChromiaLogo width={95} />
        </Grid>
        <Grid item>
          <Typography variant="h3">Chromia staking</Typography>
        </Grid>
        <Grid item className={classes.main}>
          <List>
            <ListItem disableGutters dense>
              <Box>
              Stake (freeze) CHR tokens at least for 2 weeks to participate in programs which require
              staking and earn rewards.
              <Link href={"https://blog.chromia.com/chromia-staking/"} underline="none">
                &nbsp;more
              </Link>
              </Box>
            </ListItem>
            <ListItem disableGutters dense>
              <Box>
              When tokens are staked, they cannot be withdrawn immediately. A request to withdraw must be sent at least
              2 weeks before a withdrawal can be made.
              </Box>
            </ListItem>
            <ListItem disableGutters dense>
              <Box>
                Staked tokens will receive governance reward with 25% APY for the period from the day they
                are staked until the end of 2020 under condition that user will sign into Chromia Proposals site
                before end of 2020.
              </Box>
            </ListItem>
          </List>
        </Grid>
        {!provider && (
            <Typography color="error" variant="h3">
              MetaMask not detected
            </Typography>
        )}
        {provider && !haveAccounts && (
            <Typography color="error" variant="h3">
              Please connect MetaMask
            </Typography>
        )}
        {provider && haveAccounts && selectedAddress && !operational && (
            <Typography color="error" variant="h3">
              Error while initializing
            </Typography>
        )}
        { balances && mainContent(balances)}
        {
          errorMsg !== '' &&
              <Grid item>
                <Typography color="error" variant="h4">
                  {errorMsg}
                </Typography>
              </Grid>
        }
      </Grid>
  );
};



function App() {
  useStyles()
  return <Container><StakeMain /></Container>;
}

export default App;
