import React from 'react';
import CssBaseline from '@material-ui/core/CssBaseline'
import ReactDOM from 'react-dom';
import App from './App';
import './index.css';
import './fonts.css'
import { ThemeProvider, createMuiTheme } from '@material-ui/core';

const theme = createMuiTheme({
  props: {
    MuiButtonBase: {
      disableRipple: true,
    },
  },
  typography: {
    useNextVariants: true,
    titleFont: 'Battlefin-Black',
    fontFamily: '"NB-International", "Roboto", "Helvetica", sans-serif',
    h3: {
      fontFamily: 'Battlefin-Black',
    }
  },
  palette: {
    type: 'dark',
    background: {
      default: 'rgb(25, 21, 28)',
    },
    secondary: {
      main: 'rgb(255, 64, 93, 1)'
    }
  },
  overrides: {
    MuiLink: {
      root: {
        transition: 'color 0.2s ease-in-out',
        fontFamily: 'Battlefin-Black',
        color: 'rgb(255, 64, 93, 1)',
        '&:hover': {
          color: 'rgb(119, 196, 255);',
        }
      }
    },
    MuiButton: {
      contained: {
        borderRadius: 100,
        height: 50,
        fontWeight: 600,
        textTransform: 'none',
        fontSize: 18,
      },
    },
  }
});

ReactDOM.render(
  <React.StrictMode>
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <App />
    </ThemeProvider>
  </React.StrictMode>,
  document.getElementById('root')
);
